import React from "react";
import "./AdminAge.scss";

const clickChange = () => {
  let localitem = localStorage.getItem("x-access-token");
  if (localitem === null) {
    window.alert("  .");
    window.location = "/";
  }
};

const AdminAge = ({
  requestAdminAge,
  one,
  setOne,
  two,
  setTwo,
  three,
  setThree,
  four,
  setFour,
  five,
  setFive
}) => {
  return (
    <form onSubmit={requestAdminAge}>
      <h3 className="agebar">  </h3>
      <input
        value={one}
        onChange={e => setOne(e.target.value)}
        placeholder="10"
        className="a1"
      />
      <input
        value={two}
        onChange={e => setTwo(e.target.value)}
        className="a2"
        placeholder="20"
      />

      <input
        value={three}
        onChange={e => setThree(e.target.value)}
        placeholder="30"
        className="a3"
      />

      <input
        value={four}
        onChange={e => setFour(e.target.value)}
        placeholder="40"
        className="a4"
      />

      <input
        value={five}
        onChange={e => setFive(e.target.value)}
        placeholder="50"
        className="a5"
      />
      <button
        onClick={clickChange}
        onSubmit={requestAdminAge}
        className="save3"
      >
        
      </button>
      <div className="underbox"></div>
    </form>
  );
};
export default AdminAge;
