import React from "react";
import "./AdminPageBar.scss";

const clickChange = () => {
  let localitem = localStorage.getItem("x-access-token");
  if (localitem === null) {
    window.alert("  .");
    window.location = "/";
  }
};
const AdminPageBar = ({
  requestAdminPageBar,
  date,
  setDate,
  number,
  setNumber
}) => {
  return (
    <form onSubmit={requestAdminPageBar}>
      <h3 className="bar1">   </h3>
      <h4 className="addbar"></h4>
      <input
        placeholder=""
        className="day"
        value={date}
        onChange={e => setDate(e.target.value)}
      />
      <input
        placeholder=" "
        className="setDay"
        value={number}
        onChange={e => setNumber(e.target.value)}
      />
      <button
        className="sub"
        onClick={clickChange}
        onSubmit={requestAdminPageBar}
      >
        
      </button>
    </form>
  );
};

export default AdminPageBar;
