import React, { Component } from "react";
import { PieChart } from "@opd/g2plot-react";

class Circle extends Component {
  constructor(props) {
    super(props);
    this.state = {
      getArrayMin: []
    };
  }
  render() {
    const data = [
      {
        type: "",
        value: 343
      },
      {
        type: "",
        value: 550
      }
    ];

    const config = {
      forceFit: true,
      title: {
        visible: true,
        text: ""
      },
      description: {
        visible: true,
        text: " "
      },
      radius: 0.8,
      padding: "auto",
      data,
      color: ["#05C2AB", "#FF5152"],
      angleField: "value",
      colorField: "type",
      statistic: {
        visible: true
      }
    };
    return (
      <>
        <div>
          <section>
            <PieChart {...config} />
          </section>
        </div>
      </>
    );
  }
}

export default Circle;
