import React from "react";
import "./AdminCircle.scss";

const clickChange = () => {
  let localitem = localStorage.getItem("x-access-token");
  if (localitem === null) {
    window.alert("  .");
    window.location = "/";
  }
};

const AdminCircle = ({
  requestAdminCircle,
  female,
  setFemale,
  male,
  setMale
}) => {
  return (
    <form onSubmit={requestAdminCircle}>
      <h3 className="gender">  </h3>
      <input
        value={male}
        onChange={e => setMale(e.target.value)}
        placeholder=""
        className="male"
      />
      <input
        value={female}
        onChange={e => setFemale(e.target.value)}
        placeholder=""
        className="female"
      />
      <button
        onClick={clickChange}
        onSubmit={requestAdminCircle}
        className="fix2"
      >
        
      </button>
    </form>
  );
};

export default AdminCircle;
