import React from "react";
import "./WorldWideBar.scss";

const clickChange = () => {
  let localitem = localStorage.getItem("x-access-token");
  if (localitem === null) {
    window.alert("  .");
    window.location = "/";
  }
};
const WorldWideBar = ({
  requestWorldWideBar,
  date,
  setDate,
  number,
  setNumber
}) => {
  return (
    <form onSubmit={requestWorldWideBar}>
      <h3 className="bar2">   </h3>
      <h4 className="addbar2"></h4>
      <input
        placeholder=""
        className="date"
        value={date}
        onChange={e => setDate(e.target.value)}
      />
      <input
        className="people"
        placeholder=" "
        value={number}
        onChange={e => setNumber(e.target.value)}
      />
      <button
        className="sub2"
        onClick={clickChange}
        onSubmit={requestWorldWideBar}
      >
        
      </button>
    </form>
  );
};

export default WorldWideBar;
