import React from "react";
import "./AdminPage.scss";
import TextField from "@material-ui/core/TextField";

const clickChange = () => {
  let localitem = localStorage.getItem("x-access-token");
  if (localitem === null) {
    window.alert("  .");
    window.location = "/";
  }
};
const AdminPage = ({
  requestAdminPage,
  red,
  setRed,
  orange,
  setOrange,
  blue,
  setBlue,
  black,
  setBlack
}) => {
  return (
    <form onSubmit={requestAdminPage}>
      <h3 className="kor-title"></h3>
      <h4 className="k1"></h4>
      <TextField
        className="kor-1"
        value={red}
        onChange={e => setRed(e.target.value)}
      />

      <h4 className="k2"></h4>
      <TextField
        className="kor-1"
        value={orange}
        onChange={e => setOrange(e.target.value)}
      />

      <h4 className="k3"></h4>
      <TextField
        className="kor-1"
        value={blue}
        onChange={e => setBlue(e.target.value)}
      />

      <h4 className="k4"></h4>
      <TextField
        className="kor-1"
        value={black}
        onChange={e => setBlack(e.target.value)}
      />

      <button
        className="save"
        onClick={clickChange}
        onSubmit={requestAdminPage}
      >
        
      </button>
    </form>
  );
};

export default AdminPage;
