import React from 'react';
import { FaArrowLeft } from 'react-icons/fa';
import './NewsInsert.scss';

const NewsInsert = ({ history }) => {
    const isToken = localStorage.getItem('x-access-token');
    if (!isToken) {
        window.alert("  .");
        history.go(-1);
    }
    return (
        <div className ="NewsInsert">
            <span className="NewsInsert-Top">
                <FaArrowLeft className ="NewsInsert-Arrow" onClick ={() => history.go(-1)}/> {/*    */}
                <span>  </span>
            </span>

            <hr />

            <div className="NewsInsert-Insert">
                <input type ="text" maxlength ="20" required className ="NewsInsert-Insert-Title" placeholder =" ." />
                <textarea className="NewsInsert-Insert-Contents" rows ="20" required placeholder =" ." />
                <button className ="NewsInsert-Button"></button>
            </div>
        </div>
    );
}

export default NewsInsert;